	module.exports = {"data":{"markdownRemark":{"html":"<p>We’re back again with Simon Date this episode, discussing the whys and wherefores of using Adapt in a real elearning shop!</p>\n<iframe src=\"https://simplecast.com/e/69688?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>If you’ve been iffy on the benefits or the evidence of the Adapt Learning tool in real environments, this episode is for you! This one (and the next one!) is one of my favorite episodes with Simon! He brings to light so many great points about why you might want to use Adapt, how they use it at his organization, and we talk realistically about some of the possible drawbacks. I’m sure you’ll enjoy this episode as much as I do! As always, check out the shownotes on the show page: <a href=\"https://dearinstructionaldesigner.simplecast.fm/\">https://dearinstructionaldesigner.simplecast.fm/</a> And don’t forget to <a href=\"itms://itunes.apple.com/us/podcast/dear-instructional-designer/id1111995456?mt=2\">leave a rating and review for us on iTunes</a>.</p>","frontmatter":{"date":"June 01, 2017","path":"/blog/dearid-episode-45-simon-date-on-adapt","title":"#DearID Episode 45: Simon Date on Adapt","tags":["Adapt","authoring tools","DearID","elearning","Simon Date"],"categories":["Dear ID Show","elearning"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":3,"src":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg","srcSet":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c7d05.jpg 203w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-784f5.jpg 405w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg 810w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-adada.jpg 1215w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-1c372.jpg 1500w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

